.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .dashboard-title {
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .tabs-list {
    display: flex;
    gap: 12px;
  }
  
  .tab-trigger {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 8px;
  }
  
  .chart-container {
    display: flex;
    justify-content: center;
  }
  